        float: none;
        padding: 30px 0 0 0 !important;
    }

    #toc-contents {
      padding: 0;
      height: 400px;
      margin-top: 180px;
      width: 0;
      padding-bottom: 10px;
      padding-top: 50px;
      position: fixed;
      z-index: 1;
      box-shadow: -5px 8px 20px 0px #1d77d3 !important;
      top: 0;
      text-align: left;
      border-right: none;
      right: 0;
      color: #1d77d3;
      background-color: #fff;
      overflow-x: hidden;
      overflow-y: scroll;
      transition: 0.5s;
    }


    #toc-contents a {
      padding-left: 32px;
      padding-bottom: 5px;
      text-decoration: none;
      font-size: 15px;
      color: #818181;
      display: block;
      transition: 0.3s;
    }

    #toc-contents ul li a {
        line-height: 10px;
    }

    br {
        display: none;
    }

    #toc-contents a:hover {
      color: #1d77d3;
    }

    #toc-contents .toc-closebtn {
      position: absolute;
      top: 0;
      font-size: 34px;
      line-height: 1;
    }

    #toc-contents .toc-closebtn:hover {
        color: #1d77d3;
    }

    .toc-openbtn {
        z-index: 111;
        top: 125px;
        position: fixed;
        box-shadow: 0 4px 9px -4px #1d77d3 !important;
        right: 0;
        font-size: 15px;
        cursor: pointer;
        background-color: #1d77d3;
        color: #fff;
        padding: 8px;
        border: none;
    }

    .toc-openbtn:hover {
      background-color: #1d77d3;
    }


}

.toc__list:before {
    content: "Table Of Contents";
    font-size: 13px;
    margin-left: -9px;
}

.toc__list {
    line-height: 1.8rem;
    font-size: 12px;
    line-height: 19px;
    padding: 9px;
}

.toc__list li {
    list-style-type: '►';
}

.toc__list li a {
    padding: 10px;
}

.toc__sublist {
    padding-left: 20px;
    display: none;
}

.toc__sublist li {
    list-style-type: '◦';
    list-style-position: inside;
}

.toc__sublist li a {
    padding: 10px!important;
}

.toc-h2:first-child {
    margin-top: 1rem;
}

#toc a:hover {
    color: #9D174D;
}

