#btn-back-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: none;
  width: 2.8125rem;
  height: 2.8125rem;
  cursor: pointer;
  border-radius: 50%;
  padding: 0;
  font-size: .875rem;
  line-height: 1.6;
  background-color: #1d77d3!important;
  color: #fff;
  box-shadow: 0 4px 9px -4px #1d77d3!important;
  font-weight: 500;
  text-transform: uppercase;
  vertical-align: bottom;
  border: 0;
  text-align: center;
  text-decoration: none;
  transition: color .15s
  ease-in-out,background-color .15s
  ease-in-out,border-color .15s
  ease-in-out,box-shadow .15s
  ease-in-out;
}

